import React from "react"
import { Link } from "gatsby"

const Menu = () => {
  return (
    <nav>
      <Link to="/">Strona Główna</Link>
      <Link to="/content">Wpisy</Link>
      <Link to="/transfers">Transfery</Link>
      <Link to="/about">O mnie</Link>
    </nav>
  )
}

export default Menu
