import React from "react"
import { StaticQuery, graphql } from "gatsby"
import styled from "styled-components"
import "../Styles/Articles.css"

const Articles = () => {
  return (
    <StaticQuery
      query={graphql`
        query {
          content {
            articles {
              title
              text
              img
              date
            }
          }
        }
      `}
      render={({ content: { articles } }) => (
        <div className="content">
          {articles.map(art => {
            const H1 = styled.h1`
              background-image: url(${art.img});
              display: block;
            `
            return (
              <div className="art">
                <H1>
                  <h1>{art.title}</h1>
                </H1>
                <p>{art.text}</p>
                <p>{art.date.slice(0, 10)}</p>
              </div>
            )
          })}
        </div>
      )}
    />
  )
}

export default Articles
